'use client';
import React, { useState, useEffect } from 'react';

export default function DailyUpdatesPage() {
  const [updates, setUpdates] = useState([]);
  const [loading, setLoading] = useState(true);

  // For the form
  const [date, setDate] = useState('');
  const [userName, setUserName] = useState('');
  const [taskTitle, setTaskTitle] = useState('');

  useEffect(() => {
    fetchUpdates();
  }, []);

  const fetchUpdates = () => {
    fetch('http://localhost:5000/api/daily-updates')
      .then((res) => res.json())
      .then((data) => {
        setUpdates(data);
        setLoading(false);
      })
      .catch((err) => {
        console.error('Error fetching updates:', err);
        setLoading(false);
      });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      // Prepare data
      const newData = {
        date,
        userName,
        tasks: [
          {
            title: taskTitle,
          },
        ],
      };

      const res = await fetch('http://localhost:5000/api/daily-updates', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newData),
      });

      if (!res.ok) {
        throw new Error(`Request failed with status ${res.status}`);
      }

      const createdUpdate = await res.json();
      console.log('Created update:', createdUpdate);

      // Reset the form
      setDate('');
      setUserName('');
      setTaskTitle('');

      // Re-fetch to show new record
      fetchUpdates();
    } catch (error) {
      console.error('Error submitting new update:', error);
    }
  };

  if (loading) return <div>Loading daily updates...</div>;

  return (
    <div>
      <h1>All Daily Updates</h1>

      {/* -- Form to create a new update -- */}
      <form onSubmit={handleSubmit} style={{ marginBottom: '2rem' }}>
        <div>
          <label>Date: </label>
          <input
            type='date'
            value={date}
            onChange={(e) => setDate(e.target.value)}
            required
          />
        </div>
        <div>
          <label>User Name: </label>
          <input
            type='text'
            value={userName}
            onChange={(e) => setUserName(e.target.value)}
            required
          />
        </div>
        <div>
          <label>Task Title: </label>
          <input
            type='text'
            value={taskTitle}
            onChange={(e) => setTaskTitle(e.target.value)}
            required
          />
        </div>
        <button type='submit'>Add Daily Update</button>
      </form>

      {updates.length === 0 ? (
        <p>No updates found.</p>
      ) : (
        <ul>
          {updates.map((update) => (
            <li key={update._id}>
              <strong>User:</strong> {update.userName} &nbsp;
              <strong>Date:</strong>{' '}
              {new Date(update.date).toLocaleDateString()}
              <div>
                <strong>Tasks:</strong>
                <ul>
                  {update.tasks.map((task, idx) => (
                    <li key={idx}>
                      {task.title} â€” {task.status || 'In Progress'}
                      {task.comments && ` (Comments: ${task.comments})`}
                    </li>
                  ))}
                </ul>
              </div>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
